import React, { useEffect } from 'react';
import { withRouter } from 'react-router';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useDispatch, useSelector } from 'react-redux';
import { axios } from '../../../http';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import CardWithBack from '../../../components/CardWithBack/CardWithBack';
import AlertMessageModal from '../../../components/UI/Model/AlertMessageModal';

import {
  addVehicleOwnerSaga,
  getVehicleOwnerByIdSaga,
  resetVehicleOwnerDetail,
} from '../../../store/actions';

import {
  NAME_REQUIRED,
  PHONE_REQUIRED,
  PASSWORD_REQUIRED,
  EMAIL_VALID,
  EMAIL_REQUIRED,
  SIMPLE_PASSWORD_VALIDATION,
} from '../../../constants/errorConstants';

const AddVehicleOwner = props => {
  const editId = props.match.params.id;
  // const [isEditValueUpdated, setIsEditValueUpdated] = useState(false);
  const dispatch = useDispatch();
  const { open } = useSelector(state => state.modal);
  const { errorMsg } = useSelector(state => state.vehicleOwner);

  const tableFilter = {
    columnName: 'name',
    orderBy: 'asc',
    search: '',
    skip: 0,
    limit: 10,
  };

  const validationSchema = Yup.object({
    name: Yup.string().trim().required(NAME_REQUIRED),
    phone: Yup.string()
      .trim()
      .min(10)
      .max(10)
      .required(PHONE_REQUIRED)
      .matches(/^[0-9]*$/, 'Enter Valid Number'),
    dob: Yup.string().required('Date of Birth Is Required'),
    email: Yup.string().email().typeError(EMAIL_VALID).required(EMAIL_REQUIRED),
    commission: Yup.string()
      .max(2)
      .matches(/^[0-9]*$/, 'Enter Valid Number')
      .required('Commission is Required'),
    password: Yup.string()
      .matches(/^[A-Za-z\d@$!%*?&]{8,}$/, SIMPLE_PASSWORD_VALIDATION)
      .required(PASSWORD_REQUIRED),
    passwordConfirm: Yup.string()
      .required(PASSWORD_REQUIRED)
      .oneOf([Yup.ref('password')], 'Password do not match'),
  });

  // const [initialValues, setInitialValues] = useState({
  //   name: '',
  //   email: '',
  //   phone: '',
  //   dob: '',
  //   password: '',
  //   passwordConfirm: '',
  //   commission: '',
  // });

  const { vehicleOwner } = useSelector(state => state.vehicleOwner);

  const submitButtonHandler = values => {
    dispatch(
      addVehicleOwnerSaga({
        history: props.history,
        formValues: values,
        tableFilter,
      }),
    );
  };

  useEffect(() => {
    if (editId) {
      dispatch(getVehicleOwnerByIdSaga({ id: editId }));
    }
    return () => {
      dispatch(resetVehicleOwnerDetail());
    };
  }, []);

  // if (vehicleOwner._id !== undefined) {
  //   if (!isEditValueUpdated) {
  //     let values = {
  //       name: vehicleOwner.name,
  //       email: vehicleOwner.email,
  //       phone: vehicleOwner.phone,
  //       dob: vehicleOwner.dob,
  //       password: '',
  //       passwordConfirm: '',
  //       commission: vehicleOwner.commission,
  //     };
  //     setInitialValues(values);
  //   }
  //   setIsEditValueUpdated(true);
  // }

  return (
    <>
      <CardWithBack
        pageTitle={
          props.history.location.pathname.includes('add')
            ? 'Add Vehicle Owner'
            : 'Edit Vehicle Owner'
        }
        dataLoadeed={typeof 1 === typeof 2}
        errorMsg={errorMsg}
      >
        <Formik
          enableReinitialize
          initialValues={{
            name: editId && vehicleOwner.name ? vehicleOwner.name : '',
            email: editId && vehicleOwner.email ? vehicleOwner.email : '',
            phone: editId && vehicleOwner.phone ? vehicleOwner.phone : '',
            dob: editId && vehicleOwner.dob ? vehicleOwner.dob : '',
            password: '',
            passwordConfirm: '',
            commission:
              editId && vehicleOwner.commission ? vehicleOwner.commission : '',
          }}
          validationSchema={validationSchema}
          onSubmit={(values, { setSubmitting }) => {
            submitButtonHandler(values);
            setSubmitting(false);
          }}
        >
          {({
            values,
            errors,
            touched,
            handleChange,
            handleBlur,
            handleSubmit,

            // isSubmitting,
          }) => (
            <form onSubmit={handleSubmit}>
              <div className="row">
                <div className="col-xs-12 col-lg-6 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Full Name :</h4>
                    <input
                      type="text"
                      name="name"
                      placeholder="Enter Full Name"
                      className="form-control"
                      value={values.name}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.name && touched.name && errors.name}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-6 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Email :</h4>
                    <input
                      type="text"
                      name="email"
                      placeholder="Enter Email"
                      className="form-control"
                      value={values.email}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.email && touched.email && errors.email}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-6 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Password : </h4>
                    <input
                      type="text"
                      name="password"
                      placeholder="Enter Password"
                      className="form-control"
                      value={values.password}
                      disabled={editId && vehicleOwner._id}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.password && touched.password && errors.password}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-6 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Confirm Password : </h4>
                    <input
                      type="text"
                      name="passwordConfirm"
                      placeholder="Enter Password"
                      className="form-control"
                      value={values.passwordConfirm}
                      disabled={editId && vehicleOwner._id}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.passwordConfirm &&
                        touched.passwordConfirm &&
                        errors.passwordConfirm}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-6 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Phone : </h4>
                    <input
                      type="text"
                      name="phone"
                      placeholder="Enter Phone Number"
                      className="form-control"
                      value={values.phone}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.phone && touched.phone && errors.phone}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-3 col-md-6 col-sm-12">
                  <div className="from-group text-left">
                    <h4 className="required-field">Date of Birth : </h4>
                    <input
                      type="date"
                      name="dob"
                      value={values.dob}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      className="form-control"
                    />
                    {/* <Datepicker
                    selected={values.dob}
                    name="dob"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    className="form-control"
                  /> */}
                    <div className="error-message">
                      {errors.dob && touched.dob && errors.dob}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-6 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">
                      Commission in Percentage(%) :
                    </h4>
                    <input
                      type="text"
                      name="commission"
                      placeholder="Enter Commission"
                      className="form-control"
                      value={values.commission}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.commission &&
                        touched.commission &&
                        errors.commission}
                    </div>
                  </div>
                </div>
              </div>
              <div className="form-group">
                <button
                  type="submit"
                  className="btn btn-primary m-l-sm m-b-md"
                  style={{ marginTop: '20px' }}
                >
                  Save
                </button>
              </div>
              <div className="error-message">{errorMsg}</div>
            </form>
          )}
        </Formik>
      </CardWithBack>
      {open && <AlertMessageModal />}
    </>
  );
};

export default SetTokenHeader(withRouter(AddVehicleOwner), axios);
